import React, { useState, useEffect, useRef } from 'react';
import './App.css';
import { gsap } from 'gsap';
import SpaceScene from './components/SpaceScene';
import Header from './components/Header';
import Hero from './components/Hero';
import Footer from './components/Footer';
import SplashScreen from './components/SplashScreen';
import backgroundMusic from './assets/music.mp3';
import AboutMe from './components/AboutMe';
import Experience from './components/Experience';
import Credits from './components/Credits';
import Words from './components/Words';

function App() {
  const [isPlaying, setIsPlaying] = useState(false);
  const [splashScreenVisible, setSplashScreenVisible] = useState(true);
  const [buttonVisible, setButtonVisible] = useState(false); 
  const [sceneLoaded, setSceneLoaded] = useState(false);

  const audioRef = useRef(new Audio(backgroundMusic));
  audioRef.current.loop = true;

  const splashScreenRef = useRef(null);

  const toggleMusic = () => {
    if (isPlaying) {
      audioRef.current.pause();
    } else {
      audioRef.current.play();
    }
    setIsPlaying(!isPlaying);
  };

  const handleEnterSite = () => {
    if (!isPlaying) {
      toggleMusic();
    }
    gsap.to(splashScreenRef.current, { duration: 3, opacity: 0, onComplete: () => setSplashScreenVisible(false) });
  };

  useEffect(() => {
    if (sceneLoaded) {
      setButtonVisible(true); 
    }
  }, [sceneLoaded]);

  useEffect(() => {
    const currentAudio = audioRef.current;

    return () => {
      currentAudio.pause();
    };
  }, []);

  return (
    <div className="App">
      <SpaceScene setSceneLoaded={setSceneLoaded} />

      {splashScreenVisible ? (
        <SplashScreen 
          onEnterSite={handleEnterSite} 
          buttonVisible={buttonVisible} 
          splashScreenRef={splashScreenRef} 
          loading={!sceneLoaded}  
        />
      ) : (
        <>
          <Header isPlaying={isPlaying} toggleMusic={toggleMusic} />
        

          <div className='wrapper'>
              <Hero />
            <AboutMe />
            <Experience />
            <Words />
            <Footer />
            <Credits />
          </div>
        </>
      )}
    </div>
  );
}

export default App;
