import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { FilmPass } from "three/examples/jsm/postprocessing/FilmPass";
import React, { useEffect, useRef } from "react";

import texture5 from "../assets/texture4.jpg";
import texture9 from "../assets/texture9.jpg";

import envMap from "../assets/texture9.jpg";

const SpaceScene = ({ setSceneLoaded }) => {
  const mountRef = useRef(null);
  const cometVisible = useRef(false);
  const cometStartTime = useRef(0);

  useEffect(() => {
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    mountRef.current.appendChild(renderer.domElement);

    const scene = new THREE.Scene();
    scene.background = new THREE.Color("#101820");

    const camera = new THREE.PerspectiveCamera(
      18,
      window.innerWidth / window.innerHeight,
      1,
      2000
    );
    camera.position.z = 12;

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.target.set(0, 0, 0);
    controls.update();

    scene.fog = new THREE.Fog(0x391809, 9, 100);

    const mainGroup = new THREE.Group();
    scene.add(mainGroup);

    const moonGroup = new THREE.Group();
    mainGroup.add(moonGroup);

    const galaxyGroup = new THREE.Group();
    scene.add(galaxyGroup);

    const composer = new EffectComposer(renderer);
    composer.addPass(new RenderPass(scene, camera));

    const filmPass = new FilmPass(0.2, 0.025, 648, false);
    composer.addPass(filmPass);

    function createLights() {
      const ambientLight = new THREE.HemisphereLight(0xffffff, 0x00a1a2, 1);
      scene.add(ambientLight);
    }

    function loadTexture(textureURL) {
      const textureLoader = new THREE.TextureLoader();
      const texture = textureLoader.load(textureURL);

      return texture;
    }

    function createEnvMap() {
      const textureLoader = new THREE.TextureLoader();
      const envMapTexture = textureLoader.load(envMap);
      envMapTexture.mapping = THREE.EquirectangularReflectionMapping;
      envMapTexture.magFilter = THREE.LinearFilter;
      envMapTexture.minFilter = THREE.LinearMipmapLinearFilter;
      envMapTexture.encoding = THREE.sRGBEncoding;
      return envMapTexture;
    }

    function createPlanet() {
      const icosahedronGeometry = new THREE.IcosahedronGeometry(
        0.7 * (2 / 3),
        5
      );
      const material = new THREE.MeshStandardMaterial({
        envMap: createEnvMap(),
        map: loadTexture(texture9),
        flatShading: false,
        roughness: 0.8,

        displacementScale: 0.0,
      });

      const icosahedron = new THREE.Mesh(icosahedronGeometry, material);

      icosahedron.castShadow = icosahedron.receiveShadow = true;

      mainGroup.add(icosahedron);
    }

    function createMoon() {
      const moonGeometry = new THREE.SphereGeometry(0.2 * (2 / 3), 32, 32);
      const moonMaterial = new THREE.MeshStandardMaterial({
        envMap: createEnvMap(),
        map: loadTexture(texture5),
      });

      const moon = new THREE.Mesh(moonGeometry, moonMaterial);
      moon.position.set(3, 0, 0);
      moon.castShadow = moon.receiveShadow = true;
      moonGroup.add(moon);
    }

    function setupStars(amount = 20000, size = 50) {
      const geometry = new THREE.BufferGeometry();
      const positions = [];
      const depth = -30;

      for (let i = 0; i < amount; i++) {
        const x = Math.random() * size - size / 2;
        const y = Math.random() * size - size / 2;
        const z = Math.random() * size - size / 2 + depth;

        positions.push(x, y, z);
      }

      geometry.setAttribute(
        "position",
        new THREE.Float32BufferAttribute(positions, 3)
      );
      geometry.computeBoundingSphere();

      const material = new THREE.PointsMaterial({
        size: 0.015,
        color: 0xffffff,
      });
      const points = new THREE.Points(geometry, material);
      galaxyGroup.add(points);
    }

    function createComet() {
      const cometGeometry = new THREE.SphereGeometry(0.02, 8, 8);
      const cometMaterial = new THREE.MeshStandardMaterial({
        emissive: 0xffffff,
        emissiveIntensity: 1,
        color: 0xffffff,
      });
      const comet = new THREE.Mesh(cometGeometry, cometMaterial);
      comet.visible = false;
      scene.add(comet);
      return comet;
    }

    const comet = createComet();

    function animate() {
      requestAnimationFrame(animate);

      mainGroup.rotation.y -= 0.006;

      moonGroup.rotation.y -= 0.0001;
      moonGroup.rotation.x += 0.0005;

      if (cometVisible.current) {
        const elapsed = Date.now() - cometStartTime.current;
        if (elapsed > 2000) {
          comet.visible = false;
          cometVisible.current = false;
        } else {
          comet.position.x -= 0.2;
          comet.position.y += 0.1;
        }
      } else if (Math.random() < 0.005) {
        cometStartTime.current = Date.now();
        comet.position.set(10, -5, -10);
        comet.visible = true;
        cometVisible.current = true;
      }

      camera.lookAt(scene.position);
      camera.updateMatrixWorld();

      composer.render();
    }

    function onWindowResize() {
      const width = window.innerWidth;
      const height = window.innerHeight;
      camera.aspect = width / height;
      camera.updateProjectionMatrix();
      renderer.setSize(width, height);
      composer.setSize(width, height);
    }

    createPlanet();
    createMoon();
    setupStars();
    createLights();
    onWindowResize();
    animate();

    setSceneLoaded(true);

    window.addEventListener("resize", onWindowResize, false);

    return () => {
      mountRef.current.removeChild(renderer.domElement);
      window.removeEventListener("resize", onWindowResize);
    };
  }, [setSceneLoaded]);

  return <div id="snow-scene-container" ref={mountRef} />;
};

export default SpaceScene;
